export default {
  data() {
    return {
      Data:{
        gaopaibudaiA:"高牌不带A",
        gaopaidaiA:"高牌带A",
        duizi2_9:"对子2-9",
        duizi10_A:"对子10-A",
        shunzi:"顺子",
        jinhua:"金花",
        shunjin:"顺金",
        baozi:"豹子",
      },
      id: 0,
      keys: "",
      loading: false,
      input: "",
      list: []
    };
  },

  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.GetServerConfig({
      key: "card_type_general.lua"
    });
    console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    console.log(res);
    Object.keys(res.card_type_general).forEach(item => {
      this.list.push({title:item,val:res.card_type_general[item]})
    });
    console.log(this.list);
    Object.keys(this.Data).forEach((item)=>{
      this.list.forEach((it)=>{
        if(item === it.title){
          it.title = this.Data[item]
        }
      })
    })
  },

  methods: {
    async submit(type) {
    if(type === 1){

 console.log(this.Data,this.list);
      Object.keys(this.Data).forEach((item)=>{
        this.list.forEach((it)=>{
          if(it.title === this.Data[item]){
            console.log("=============");
            
           this.Data[item] = it.val
            
          }
        })
      })
       console.log(this.Data,this.list);
      
      let resData = {
        card_type_general:this.Data
      }
      console.log(resData);
      
    //       let { data } = await this.$http.HallFunConfig.PutServerConfig({
    //      keys:this.keys,
    //      values:JSON.stringify(this.resData),
    //      id:this.id,
    //    });
    // console.log(data);
    // if(data.code === 1 && data.msg === 'ok'){
    //   this.$message({
    //     type: "success",
    //     message: "保存成功!"
    //   });
    // }
      }else if(type === 2){
        this.loading = true
     
       let { data } = await this.$http.HallFunConfig.PostServerConfig({
         keys:this.keys,
         values:JSON.stringify(this.resData),
         id:this.id,
       });
      console.log(data);
    //   if(data.code === 1 && data.msg === 'ok'){
    //     this.loading = false
    //   this.$message({
    //     type: "success",
    //     message: "发送服务器配置成功!"
    //   });
    // }

      }
    },
    
  }
};



